import {
    FETCH_GET_BURNUP,
    FETCH_GET_BURNDOWN,
} from '../../constants/api/burncharts';

export const fetchGetBurnup = id => ({
    type: FETCH_GET_BURNUP,
    fetch: true,
    url: `/charts/${id}/burnup_chart`,
    params: {
        method: 'GET',
    },
});

export const fetchGetBurndown = id => ({
    type: FETCH_GET_BURNDOWN,
    fetch: true,
    url: `/charts/${id}/burndown_chart`,
    params: {
        method: 'GET',
    },
});
