import {
  FETCH_GET_ALL_MEMBERS,
  FETCH_TASK_ASSIGN,
  FETCH_TASK_UNASSIGN,
  FETCH_UPDATE_MEMBER,
  FETCH_DELETE_MEMBER,
  FETCH_QUIT_PROJECT,
} from '../../constants/api/members';

export const fetchProjectMembers = id => ({
  type: FETCH_GET_ALL_MEMBERS,
  fetch: true,
  url: `/projects/${id}/members/`,
  params: {
    method: 'GET',
  },
});

export const fetchTaskAssign = (id, body) => ({
  type: FETCH_TASK_ASSIGN,
  fetch: true,
  url: `/tasks/${id}/assign/`,
  params: {
    method: 'POST',
    body,
  },
});

export const fetchTaskUnassign = (id, userId) => ({
  type: FETCH_TASK_UNASSIGN,
  fetch: true,
  url: `/tasks/${id}/users/${userId}/unassign/`,
  params: {
    method: 'DELETE',
  },
});

export const fetchUpdateMember = (projectId, memberId, body) => ({
  type: FETCH_UPDATE_MEMBER,
  fetch: true,
  url: `/projects/${projectId}/members/${memberId}/`,
  params: {
    method: 'PUT',
    body,
  },
});

export const fetchDeleteMember = (projectId, memberId) => ({
  type: FETCH_DELETE_MEMBER,
  fetch: true,
  url: `/projects/${projectId}/members/${memberId}/`,
  params: {
    method: 'DELETE',
  },
});

export const fetchQuitMember = (projectId, memberId) => ({
  type: FETCH_QUIT_PROJECT,
  fetch: true,
  url: `/projects/${projectId}/members/${memberId}/`,
  params: {
    method: 'DELETE',
  },
});
