import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';

import 'react-day-picker/lib/style.css';

import CircularProgress from '../../../lib/CircularProgress';
import { notifyError } from '../../../../actions/notification';

import {
  fetchGetBurnup,
} from '../../../../actions/api/burncharts';

import BurnUp from './Burnup';

import language from '../../../../constants/language';

/* eslint-disable react/no-unused-state */
const styles = {
  wrapper: {
    height: '400px',
  },
  modalContent: {
    width: '500px',
    height: '400px',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
  },
  container: {
    height: '700px',
  },
  modalContainer: {
    height: '700px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  graph: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
  }
};

class BurnUpContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      data: '',
    };
    this.changeData = this.changeData.bind(this);
  }

  componentDidMount() {
    this.changeData(this.props.milestone);
  }

  changeData(id) {
    this.props
      .fetchGetBurnup(id)
      .then((response) => {
        var output = {}
        output = response.body
        var estimated_hours = {}
        estimated_hours['id'] = language[this.props.selectedLanguage].burnchart.estimated_hours
        estimated_hours['data'] = response.body.hour_stats[2]['data']

        var reported_hours = {}
        reported_hours['id'] = language[this.props.selectedLanguage].burnchart.reported_hours
        reported_hours['data'] = response.body.hour_stats[0]['data']

        var goal = {}
        goal['id'] = language[this.props.selectedLanguage].burnchart.goal
        goal['data'] = response.body.hour_stats[1]['data']

        output['hour_stats'] = [reported_hours, estimated_hours, goal]

        var legends = [language[this.props.selectedLanguage].burnchart.hours, language[this.props.selectedLanguage].burnchart.days]
        output['legends'] = legends

        this.setState({
          loading: false,
          data: output,
        });
      });
  }
  render() {
    const { loading } = this.state;
    if (loading) {
      return <CircularProgress />;
    }
    const { classes } = this.props;
    const { data } = this.state
    return (
      < div className={classes.container} >
        <div className={classes.headerContainer}>
          <Typography className={classes.title}>
            {language[this.props.selectedLanguage].burnchart.burnup}
          </Typography>
          <div className={classes.line} />
          <div style={{ display: 'flex', alignItems: 'center', margin: '10px' }}>
            <Typography className={classes.text}>
              {language[this.props.selectedLanguage].burnchart.milestoneSelection}:{' '}{this.props.milestone}
            </Typography>
          </div>
        </div>
        <div style={styles.graph} className={classes.wrapper}>
          {data === '' && (
            <Typography style={{ textAlign: 'center', textColor: '#2a3d41' }}>
              {' '}
              {language[this.props.selectedLanguage].burnchart.noMilestoneSelected}{' '}
            </Typography>
          )}
          {data !== '' && data.task_number !== 0 && <BurnUp graph={data.hour_stats} leg={data.legends} />}
          {data !== '' && data.task_number === 0 && (
            <Typography style={{ textAlign: 'center', textColor: '#2a3d41' }}>
              {' '}
              {language[this.props.selectedLanguage].burnchart.noReportedHoursInMilestone}{' '}
            </Typography>
          )}
        </div>
      </div >
    );
  }
}

BurnUpContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  milestone: PropTypes.string.isRequired,
  notifyError: PropTypes.func.isRequired,
  fetchGetBurnup: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};


const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchGetBurnup,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(BurnUpContainer));
