import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { selectProject } from '../../actions/user';
import { fetchProjectMembers } from '../../actions/api/members';

import CircularProgress from '../../components/lib/CircularProgress';

import BurnupReportsContainer from '../../components/report/share/BurnUp';

class Report extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
        };
    }

    componentDidMount() {
        this.props.selectProject(this.props.projectId);
        this.props.fetchProjectMembers(this.props.projectId)
            .then(() => {
                this.setState({ loading: false });
            });
    }

    render() {
        if (this.state.loading) {
            return <CircularProgress />;
        }
        return (
            <Fragment>
                <BurnupReportsContainer projectId={this.props.projectId} milestone={this.props.milestone} />
            </Fragment>
        );
    }
}

Report.propTypes = {
    projectId: PropTypes.string.isRequired,
    milestone: PropTypes.string.isRequired,
    selectProject: PropTypes.func.isRequired,
    fetchProjectMembers: PropTypes.func.isRequired,
};

function mapStateToProps(state, ownProps) {
    return {
        projectId: String(ownProps.match.params.id),
        milestone: String(ownProps.match.params.milestone_id),
    };
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectProject,
        fetchProjectMembers,
    }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Report);
